@font-face {
    font-family: 'GOST UI 2 Regular';
    src: url('../fonts/GOST UI 2.eot');
    src: url('../fonts/GOST UI 2.eot?#iefix') format('embedded-opentype'),
         url('../fonts/GOST UI 2.woff') format('woff'),
         url('../fonts/GOST UI 2.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'San Francisco Pro Display Regular';
    src: url('../fonts/SFProDisplay-Regular.eot');
    src: url('../fonts/SFProDisplay-Regular.eot?#iefix') format('embedded-opentype'),
         url('../fonts/SFProDisplay-Regular.woff') format('woff'),
         url('../fonts/SFProDisplay-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}